import React from "react";
import { CursusList } from "../helpers/CursusList";
import CursusItem from "../components/CursusItem";
import "../styles/CursusAanbod.css";

function CursusAanbod() {
  return (
    <div className="cursus">
      <h1 className="cursusTitle">Onze cursussen</h1>
      <div className="cursusList">
        {CursusList.map((cursusItem, key) => {
          return (
            <CursusItem
              key={key}
              image={cursusItem.image}
              name={cursusItem.name}
              price={cursusItem.price}
            />
          );
        })}
      </div>
    </div>
  );
}

export default CursusAanbod;