import React from "react";
//import from "../assets/multiplePizzas.jpeg";
import "../styles/About.css";

function About() {
  return (
    <div className="about">
      <div
        className="aboutTop"
        //style={{ backgroundImage: `url(${MultiplePizzas})` }}
      >
         <h1> Over Ons </h1>
         <h2> Raymond Lahaije </h2>
      </div>
      <div className="aboutBottom">
<h3> Raymond Lahaije is mijn naam en ik ben gecertificeerd kynologisch instructeur, kynologisch gedragsdeskundige en gecertificeerde Pijn- en voedingscoach voor Honden bij Priority Dogs. In mijn beleving word je met een hart voor honden (dieren in het algemeen) geboren. Bij de een gaat dat iets verder dan bij de ander. Bij mij misschien wel iets te ver soms.
  </h3>
<h3>Als kind groeide ik op met diverse honden en nu realiseer ik me dat er toen nog heel anders tegen honden werd aangekeken. Inmiddels zijn we in de hondenwereld een stuk wijzer !
 <br />
 <br /> 
  Hopelijk zijn mensen zich ervan bewust dat je niet bij iedereen terecht kunt voor hulp met de hondenopvoeding. 
  <br /> 
  <br />
  Om hierin zo veelzijdig mogelijk te zijn heb ik diverse opleidingen gevolgd in België en Nederland, zowel op medisch- als op gedragsdeskundig gebied. Het vergt de nodige jaren aan studie en ervaring en toch ben ik nog lang niet uitgeleerd.
<br /> 
<br />
Sinds enkele jaren word ik ingeschakeld door diverse dierenartspraktijken wanneer er probleemgedrag gemeld wordt. Soms zijn mensen de wanhoop nabij en is hulp op maat een vereiste. U zult vast begrijpen dat kennis en ervaring uit een boekje tekort schieten.
Mijn vrouw en ik hebben momenteel 2 Västgötaspetsen (Zweedse Herders) Frida en Björna. We hebben een hele tijd vakantie opvang voor honden gedaan waardoor er inmiddels zo’n 90 honden ons huis van binnen gezien hebben (en zeker de helft hiervan heeft op de bank gelegen).
<br /> 
<br />
In het verleden was ik werkzaam als vrijwilliger bij een Kynologen club.
Gezien ik iets meer wilde dan de methode aldaar, ben ik in 2016 een eigen hondenopleidingscentrum begonnen. Wat ik eigenlijk niet gepland had is in no time tempo werkelijkheid geworden……………Priority Dogs!
</h3> <h2>AND I LOVE IT !</h2>
<br />
<h2>Diana Stakenburg</h2>
<h3>Ik ben instructeur puppycursus en gehoorzaamheid
  <br />
  <br />
   Mijn naam is Diana en woon samen met Raymond, onze 3 honden en 1 kat in Nieuwstadt. Als kind groeide ik op met een boxer en toen is mijn liefde voor honden al ontstaan. 
   <br />
   <br />
   Honden zijn niet weg te denken uit ons leven en ons huis is dan ook volledig ingericht (met de nodige foto’s) op het houden van honden, zeker nu we onze nieuwe aanliggende lesruimte ingericht hebben. 
   <br />
   <br />
   Gaandeweg leerde ik steeds meer van en over onze viervoeters. Het bezoeken van shows en volgen van gehoorzaamheidscursussen bracht mij in contact met veel rassen en hun eigenschappen. Ook de vele logeetjes zorgden ervoor dat ik mij steeds meer ging verdiepen in het hondengedrag. 
   <br />
   <br />'
   Toen mij de mogelijkheid geboden werd om les te gaan geven in de hondenschool, heb ik deze kans direct met beide handen aangegrepen.
   <br />
   <br />
    Het begeleiden van hondeneigenaren naar een leuk samenspel met hun hond, geeft veel voldoening en positieve energie. Ik hoop dit dan ook nog lang te kunnen blijven doen en mij steeds verder te ontwikkelen. 
    <br />
    <br />
    Momenteel heb ik mij hoofdzakelijk toegelegd op de puppycursus en probeer ik de puppy-eigenaren een goede start te geven. Daarom blijf ik mij ontwikkelen in de kynologie door workshops en lezingen te volgen, waarvan ik er in het verleden al een paar hele leuke en goed toepasbare gevolgd heb. 
    <br />
    <br />
    Inmiddels ben ik intern opgeleid tot Kynologisch instructeur.</h3>

 </div>
  </div>
);
}

export default About;